<template>
  <ec-container title="码包申请浮动比例管理" icon="onetomany" color-code="001">
    <ec-container-body>
      <ec-panel>
        <ec-panel-toolbar>
          <ec-panel-item>
            <ec-query-panel :show-action="false">
              <ec-query-item label="调拨年度">
                <a-input-number v-model="curYear"></a-input-number>
                <!-- <ec-select showSearch @select="getTableData" placeholder="请选择" v-model="curYear">
                  <a-select-option v-for="item in prodYear" :key="item.value" :value="item.value">
                    {{ item.name }}
                  </a-select-option>
                </ec-select> -->
              </ec-query-item>
            </ec-query-panel>
          </ec-panel-item>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-body>
          <ec-v-layout>
            <ec-v-item>
              <ec-table
                ref="table"
                :data="tableData"
                :loading="loading"
                :edit-config="{ trigger: 'click', mode: 'row' }"
              >
                <ec-table-col width="80" type="checkbox" align="center"></ec-table-col>
                <ec-table-col title="序号" width="80" type="seq" align="center"></ec-table-col>
                <ec-table-col title="业务类型" field="applyType" align="center"></ec-table-col>
                <ec-table-col
                  title="浮动比例 (%)"
                  field="floatPercent"
                  align="center"
                  min-width="150"
                  :edit-render="{}"
                >
                  <template #edit="{ row }">
                    <a-input-number v-model="row.floatPercent" />
                  </template>
                </ec-table-col>
              </ec-table>
            </ec-v-item>
          </ec-v-layout>
        </ec-panel-body>
        <ec-panel-footer>
          <ec-panel-item-right>
            <a-button
              v-if="busiYear === curYear"
              :disabled="!tableData.length"
              :loading="saveLoading"
              type="primary"
              @click="onSave"
              >保存</a-button
            >
          </ec-panel-item-right>
        </ec-panel-footer>
      </ec-panel>
    </ec-container-body>
  </ec-container>
</template>
<script>
export default {
  name: 'SpecFloat',
  data() {
    return {
      curYear: new Date().getFullYear(),
      saveLoading: false,
      tableData: [],
      loading: false,
      selectData: [],
    };
  },
  computed: {
    _api() {
      return this.$apis.packingSpec.apply;
    },
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.SM_YEAR;
    },
  },
  watch: {
    curYear(val) {
      if (val) {
        this.getTableData();
      }
    },
  },
  methods: {
    onSave() {
      this.saveLoading = true;
      this._api
        .updatePcTCpApplyFloatPer({ pcTCpApplyFloatPerDTOS: this.tableData })
        .then(() => {
          this.$message.success('保存成功');
        })
        .finally(() => {
          this.saveLoading = false;
          this.getTableData();
        });
    },
    getTableData() {
      this.loading = true;
      this._api
        .listPcTCpApplyFloatPerByIds({ tranYear: this.curYear })
        .then((data) => {
          this.tableData = data?.rows;
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },
  created() {
    this.getTableData();
  },
};
</script>
<style lang="less">
.moduDrawer {
  .ant-col {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    span {
      padding-left: 5px;
    }
  }
}
.moduDrawer .mouldContent {
  padding: 16px 45px 16px 16px;
  background-color: #f2f4f5;
  border-radius: 6px;
  min-height: 300px;
  overflow-y: auto;
  margin: 5px 0 8px 0;
  color: #393b3d;
  font-weight: 700;
}
</style>
